import { Component } from '@angular/core';
import { NavController, IonicPage } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  private cards: any[] = [{
    title: 'Project: Cone',
    img: 'assets/imgs/01_1.jpg',
    content: '자유로운 콘텐츠 생산자들',
    page: 'ProjectConePage'
  }, {
    title: '코딩',
    img: null,
    content: 'test text',
    page: 'PanelListPage'
  }, {
    title: '그래픽',
    img: null,
    content: 'test text',
    page: 'PanelListPage'
  }, {
    title: '여가',
    img: null,
    content: 'test text',
    page: 'PanelListPage'
  }];

  constructor(public navCtrl: NavController,
  ) {
  }

  async downloadFileWithCount() {
    window.open('https://is2you2.github.io/assets/data/game/starcraft/Multi(R)/(3)Multi[R].scx', '_self', 'location=yes');
  }
  async downloadFileWithCount2() {
    window.open('https://is2you2.github.io/assets/data/game/starcraft/Mixed(R)/(3)Mixed[O].scx', '_self', 'location=yes');
  }
}



